$background: rgb(22, 189, 161);
$color: rgb(0, 0, 0);

Header {
  h1 {
    color: $color;
    background: $background;
    text-align: center;
  }
}

section {
  text-align: center;
  font-size: 20px bold;
  margin: 100px auto;
  span {
    background: $background;
    color: $color;
    padding: 1em 1.5em;
    box-shadow: 0 5px 0 rgb(28, 214, 167);
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    margin:10px 10px;

    &.positive {
      background: red;
      border-radius: 50%;
    }

    &.negative {
      background: blue;
      color: white;
    }
  }
  h4{
    text-align: center;
    font-size: 1.5em;
  }

  a {
    background: $background;
    color: $color;
    padding: 1em 1.5em;
    box-shadow: 0 5px 0 rgb(28, 214, 167);
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    margin:10px 10px;
  }

  a:hover {
    background-color: rgb(14, 236, 107);
  }
  
  a:active {
    background-color: black;
  }
  
  a:visited {
    background-color: #ccc;
  }
}

Footer{
  bottom: 0;
  left:0;
  position: fixed;
  text-align: center;
  background: $background;
  color: $color;
  width:100%;
}